import QuizState from './context/QuizConext/QuizState';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from 'react-router-dom';
import HomeComponent from './HomeComponent/HomeComponent';
import QuizComponent from './QuizComponent/QuizComponent';
import NavBarComponent from './NavBarComponent/NavBarComponent';
import './App.css';
function App() {
  return (
    <QuizState>
      <Router>
        <Switch>
          <div className='App'>
            <NavBarComponent />
            <Route exact path='/quizat' component={HomeComponent} />
            <Route exact path='/quizat/:id' component={QuizComponent} />
            <Route path='*' render={() => <Redirect to='/quizat' />} />
          </div>
        </Switch>
      </Router>
    </QuizState>
  );
}

export default App;
